<template>
    <article class="service" v-if="svc">
        <div class="kicker"><fa-icon name='cog'/> Tjeneste</div>
        <h1>{{svc.title}}</h1>
        <p v-if="'summary' in svc.text" class="lead">{{svc.text.summary}}</p>
        <p v-if="'description' in svc.text">{{svc.text.description}}</p>
        <p>State: {{svc.info.service_state}}</p>
    </article>
</template>

<script>
import FaIcon from '@/components/FaIcon.vue';
import tkdata from '@/tkdata';

export default {
    name: 'Service',
    props: {
        svcId: { type: String, required: true }
    },
    data() {
        return {
            svc: null
        }
    },
    created() {
        tkdata().then(data => {
            this.svc = data.service[this.svcId];
        });
    },
    components: {
        FaIcon
    }
}
</script>

<style scoped>
    article.service {
        x-margin: 2ex 0;
    }
    .kicker {
        font-size: 80%;
        color: #cf3c3a;
        text-transform: uppercase;
    }
    h1 {
        font-size: 130%;
    }
    p {
        margin: 1ex 0;
    }
    .lead {
        font-style: italic;
    }

</style>